import Vue from 'vue';
import App from './App.vue';
import { extend, ValidationProvider, ValidationObserver } from 'vee-validate/dist/vee-validate.full.min';
import validationRules from './modules/validation';

const rules = {
    ...validationRules,
};

Vue.component('ValidationProvider', ValidationProvider);
Vue.component('ValidationObserver', ValidationObserver);
Object.entries(rules).forEach((validation) => extend(...validation));

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
}).$mount('#app')
