<template>
  <div id="app">
    <div class="h4">
      Your money transfer:
      <span v-if="!isErrorSum" class="small">{{ money | cents }} cents</span>
      <span v-else>Forbidden. Is not money.</span>
    </div>
    <ValidationObserver>
      <div class="row">
        <div class="col-6">
          <ValidationProvider
            v-slot="{ errors }"
            name="money"
            rules="required|regex"
          >
            <input-money-validate
              label="Transfer money"
              type="text"
              v-model="money"
              :error-text="(isErrorSum = errors[0])"
              placeholder="Enter the amount..."
            />
          </ValidationProvider>
        </div>
      </div>
    </ValidationObserver>
  </div>
</template>

<script>
import InputMoney from "./components/InputMoney";

export default {
  name: "App",
  components: {
    "input-money-validate": InputMoney,
  },

  filters: {
    cents(value) {
      if (!value) return "0";

      const sumbol = [",", "."];
      const isLastTwoNum = sumbol.includes(value.slice(-3, -2));
      const isLastOneNum = sumbol.includes(value.slice(-2, -1));

      if (isLastTwoNum) {
        value = value.replace(/[,.]/g, "");
      } else if (isLastOneNum) {
        value = value.replace(/[,.]/g, "") + "0";
      } else {
        value = value.replace(/[,.]/g, "") + "00";
      }

      return value;
    },
  },

  data: () => ({
    money: "",
    isErrorSum: "",
  }),
};
</script>

