<template>
  <div :class="classes" class="form-group">
    <label v-if="label" :for="`el_${_uid}`">
      {{ label }}
      <span v-if="required" class="required">*</span>
    </label>
    <input
      :id="`el_${_uid}`"
      :ref="`el_${_uid}`"
      :disabled="disabled"
      :name="inputName"
      :title="title"
      :type="type"
      :class="inputClass"
      :value="val_money"
      @input="valueSum"
    />
    <div v-if="errorText" class="error">
      {{ errorText }}
    </div>
  </div>
</template>

<script>
export default {
  name: "InputMoney",

  model: {
    prop: "val_money",
    event: "change_money",
  },

  props: {
    inputClass: {
      type: String,
      default: "",
    },

    label: {
      type: String,
      default: "",
    },

    type: {
      type: String,
      default: "text",
    },

    val_money: {
      type: String,
      default: "",
    },

    placeholder: {
      type: String,
      default: "",
    },

    disabled: {
      type: Boolean,
      default: false,
    },

    title: {
      type: String,
      default: "",
    },

    name: {
      type: String,
      default: "",
    },

    errorText: {
      type: String,
      default: "",
    },

    required: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    classes() {
      return {
        "has-error": this.errorText,
      };
    },
    inputName() {
      return this.name || `${this.type}-${this._uid}`;
    },
  },

  methods: {
    valueSum(e) {
      let val = e.target.value;
      val = val.replace(/[^\d,.]/g, "");
      this.$refs[`el_${this._uid}`].value = val;
      this.$emit("change_money", val);
    },
  },
};
</script>

